    .contact__box {
        font-size: 2rem;
        width: 100%;
    }

    .contact__box a {
        padding: 0;
    }

    .contact__box p:hover {
        text-decoration-line: underline;
        text-decoration-color: var(--khaki);
        text-decoration-thickness: 5px;
        text-underline-offset: var(--m);
        text-decoration-skip-ink: none;

    }

    .contact__bg {
        min-height: 300px;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
    }


    .contact__bg img {
        transform: scaleX(-1) translateY(30%);

    }


    footer {
        background: var(--black);
        display: flex;
        justify-content: center;
        width: 100%;

    }

    .footer__container {
        overflow: hidden;
        min-height: 200px;
        height: 100px;
        padding: var(--l);
        /* outline: 15px solid green; */

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-items: center;


    }

    .footer__nav {
        flex: 1 1 25%;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100px;
        /* outline: 5px solid green; */


    }

    .footer__nav li {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        /* outline: 5px solid green; */

    }


    .footer__nav a:hover {
        text-decoration-line: underline;
        text-decoration-color: var(--khaki);
        text-decoration-thickness: 4px;
        text-underline-offset: var(--m);
    }

    .footer__go-up {
        flex: 1 1 30px;
        display: flex;
        justify-content: center;
        /* outline: 5px solid green; */

    }

    .footer__copyrightinfo {
        padding: var(--l) 0;
        flex: 2 0 50%;
        width: 100%;
        text-align: center;
    }

